.main.container{
    margin:0 1.5rem;
    margin-bottom: 2rem;
}

@media screen and (max-width: 500px){
    .main.container{
        margin: 0 -0.25rem;
        margin-bottom: 1rem;
    }
}

.loader-container {
    display:flex;
    align-items:center;
    justify-content:center;
    flex-direction:column;
    height:80vh;
}

.loader {
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 20px;
}

@keyframes load {
    50% {
        transform: rotateZ(180deg) scale(0.2);
        border-color: orange;
    }

    100% {
        transform: rotateZ(360deg) scale(1.1);
    }
}

@keyframes load10 {
    50% {
        transform: rotateZ(180deg);
        border-style: dashed;
        border-color: red maroon indianred orangered;
    }

    100% {
        transform: rotateZ(360deg);
    }
}

.loader10::before {
    content: "";
    color: white;
    height: 100px;
    width: 100px;
    background: transparent;
    border-radius: 50%;
    border: 20px solid red;
    animation: load10 1s infinite;
}
.main.container {
    margin: 0 1.5rem;
    margin-bottom: 2rem;
}

@media screen and (max-width: 500px) {
    .main.container {
        margin: 0 -0.25rem;
        margin-bottom: 1rem;
    }
}

@keyframes spin3D {
    from {
        transform: rotate3d(.5,.5,.5, 360deg);
    }

    to {
        transform: rotate3d(0,0,0,0);
    }
}

.loader-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.spinner-box {
    width: 300px;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
}

.leo {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}

.blue-orbit {
    width: 165px;
    height: 165px;
    border: 2px solid #91daffa5;
    -webkit-animation: spin3D 3s linear .2s infinite;
}

.green-orbit {
    width: 120px;
    height: 120px;
    border: 2px solid #91ffbfa5;
    -webkit-animation: spin3D 2s linear 0s infinite;
}

.red-orbit {
    width: 90px;
    height: 90px;
    border: 2px solid #ffca91a5;
    -webkit-animation: spin3D 1s linear 0s infinite;
}

.white-orbit {
    width: 60px;
    height: 60px;
    border: 4px solid #ffffff;
    -webkit-animation: spin3D 10s linear 0s infinite;
}

.w1 {
    transform: rotate3d(1, 1, 1, 90deg);
}

.w2 {
    transform: rotate3d(1, 2, .5, 90deg);
}

.w3 {
    transform: rotate3d(.5, 1, 2, 90deg);
}